@import '../../gacolors.scss';

.recipient-list-item {
    display: flex;
    align-items: center;
    padding: 0.5rem;
    cursor: pointer;
    overflow: hidden;
    border-bottom: 1px solid $ga-light-gray;

    &:hover {
        background-color: rgb(var(--contrast-300));
    }

    &.disabled {
        opacity: 0.7;
        filter: grayscale(1);
        -webkit-filter: grayscale(1);
    }

    .recipient-icon {
        display: flex;
        align-items: center;
        margin-right: 1rem;
        padding: 0.5em;
        border-radius: 50%;
        background-color: $ga-blue;
        color: $ga-white;

        &.coworker {
            background-color: $ga-orange;
        }
    }

    .recipient-info-container {
        display: flex;
        flex-direction: column;
        font-size: 0.7rem;
        flex-grow: 2;

        .recipient-info-contact-data {
            display: flex;
            flex-wrap: wrap;
            overflow: hidden;

            .recipient-phone {
                &:empty {
                    display: none;
                }
            }

            &.contact--email {
                .recipient-phone {
                    &::before {
                        content: '|';
                        margin: 0 0.5rem;
                    }
                }
            }
        }
    }

    .recipient-add-button {
        color: $ga-orange;
    }
}
