import { Component, h, Prop } from '@stencil/core';
import { IRecipient } from '../../types/Recipient';

@Component({
    tag: 'recipient-item',
    shadow: true,
    styleUrl: 'recipient-item.scss',
})
export class RecipientItem {
    @Prop() recipient: IRecipient;
    @Prop() showAdd: boolean = true;

    public render() {
        const { fullname, email, mobile, limetype, company } = this.recipient;
        const icon: string = this.getIcon(limetype);
        const recipientList: string = `recipient-list-item ${this.isDisabled()}`;
        const contactInfoClasses: string = `recipient-info-contact-data${
            email ? ' contact--email' : ''
        }${mobile ? ' contact--phone' : ''}`;

        return (
            <li class={recipientList}>
                <div class={`recipient-icon ${limetype}`}>
                    <limel-icon name={icon} size="small" />
                </div>
                <div class="recipient-info-container">
                    <span>{fullname}</span>
                    <div>
                        <span>{company}</span>
                    </div>
                    <div class={contactInfoClasses}>
                        <span class="recipient-email">{email}</span>
                        <span class="recipient-phone">{mobile}</span>
                    </div>
                </div>
                {this.renderAddIcon(this.showAdd)}
            </li>
        );
    }

    private renderAddIcon(show: boolean) {
        return show ? (
            <div class="recipient-add-button">
                <limel-icon name="add" size="medium" />
            </div>
        ) : (
            []
        );
    }

    private getIcon(limetype: string): string {
        return limetype === 'coworker' ? 'school_director' : 'guest_male';
    }

    private isDisabled(): string {
        return !this.recipient.email && !this.recipient.mobile
            ? 'disabled'
            : '';
    }
}
